@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
.body{
  font-family: 'Poppins';
}

.timeline-wrapper {
 margin-left: 2rem;
  border-left: 1px solid #909090;
}
.node {
  padding-left: 3rem;
  padding-bottom: 1.5rem;
  position: relative;
}
.node h3, .node p {
  margin: 0;
}
.node::before {
  content: "";
  width: 1rem;
  height: 1rem;
  background: #A02C2C;
  border: 2px solid #000;
  border-radius: 50%;
  position: absolute;
  top: .3rem;
  left: -8.5px;
}




.nav {
 
  position: -webkit-sticky;
 
  position: sticky;
  top:10px;
  
  height: 0;
  background-color: 'red';
  z-index: 1000;
}
.active{
  color: #A02C2C;
  border-bottom: 1.3px solid #A02C2C;
}

.nav .nav-content {
  max-width: 00px; 
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 110%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  color: white;
}

.nav-item:hover {
  color: #A02C2C;
  cursor: pointer;
}
